<script setup lang="ts">
import { useI18n } from "vue-i18n";
import { CityPaths } from "@enums/routes/user-web.routes";
import type { PaginationState } from "types/common/pagination";
import { onMounted } from "vue";
import { useLandingPageStore } from "@user-web/stores/modules/landing-page";
import { AutoCompleteReturnParamTypes } from "@enums/search/search.enum";
import AllServices from "@user-web/views/auth/LandingPage/components/MainSection/AllServices.vue";

const { t } = useI18n();
const landingPageStore = useLandingPageStore();

const searchCities = async (search: string) => {
  landingPageStore.isSearchingForCities = true;
  await landingPageStore.searchForCityOrPage(search, AutoCompleteReturnParamTypes.CITIES);
  landingPageStore.isSearchingForCities = false;
};
const loadMore = async ($paginationState: PaginationState) => {
  if (landingPageStore.citiesPagination?.nextUrl) {
    await landingPageStore.getMoreCityNames($paginationState);
  }
};

onMounted(async () => {
  await landingPageStore.getCityNames(100);
});
</script>

<template>
  <AllServices
    v-if="landingPageStore.cityNames?.length"
    :title="t('generics.all_items', { items: t('global.city', 2) })"
    :search-placeholder="t('city.search_city')"
    :subtitle="`${landingPageStore.citiesPagination?.totalCount} ${t('generics.connected_item', {
      item: t('global.cities_and_municipalities'),
    })}`"
    :routing-path="CityPaths.CITY[1]"
    :empty-state-description="
      t('generics.no_found_items', { item: t('global.cities_or_municipalities') })
    "
    :service-names="landingPageStore.cityNames"
    type="cities"
    @infinite="loadMore"
    @search="searchCities"
  />
</template>
